.card-background:after {
  border: 0.1px solid transparent;
  border-image-source: var(--card-border);
  border-image-slice: 60;
  border-image-width: 60;
  border-image-repeat: stretch;
  opacity: 0.5;
}
@-moz-document url-prefix() {}

.main-nav {
  position: fixed;
  top: 50%;
  transform: translateY(-50%);
  left: clamp(25px, 5.385vw, 70px);
  z-index: 20;
}
.main-nav__list {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  grid-gap: 8px;
}
.main-nav__link {
  font-family: var(--font-family2);
  font-style: normal;
  font-weight: 700;
  font-size: clamp(20px, 3.846vw, 50px);
  line-height: 100%;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  background-clip: text;
  color: transparent;
  -webkit-text-stroke: 1px #fff;
  -webkit-text-fill-color: transparent;
  position: relative;
  transition: all 0.4s;
  background-color: transparent;
}
.main-nav__item.active .main-nav__link {
  background-color: #fff;
}
.main-nav__item:not(:last-child):hover .main-nav__link {
  background-color: #fff;
}
.main-nav__item:last-child .main-nav__link {
  font-family: var(--font-family);
  color: #C3018D;
  letter-spacing: 0.02em;
  -webkit-text-stroke: initial;
  -webkit-text-fill-color: initial;
  background-clip: initial;
}
@media screen and (min-width: 1025px) and (min-height: 700px) {
  .main-nav {
    position: absolute;
  }
}

.main-top {
  display: flex;
  flex-direction: column;
  grid-gap: 10px;
}
.main-top__title {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 700;
  font-size: clamp(32px, 3.472vw, 50px);
  line-height: 110%;
  color: var(--color-a);
}
.main-top__subtitle {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 400;
  font-size: clamp(16px, 1.389vw, 20px);
  line-height: 110%;
  color: var(--color-a);
}

.social-list {
  display: flex;
  align-items: center;
  grid-gap: clamp(12px, 1.389vw, 20px);
}
.social-list__link {
  transition: all 0.4s;
  color: var(--color-a);
}
.social-list__link:hover {
  color: var(--color-c);
}
.social-list svg {
  fill: currentColor;
}

.mini-card {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 20px 25px;
  height: 100%;
}
.mini-card__btn {
  position: absolute;
  right: 28px;
  top: 28px;
  z-index: 2;
}
.mini-card__icon {
  margin-bottom: 19px;
}
.mini-card__title {
  margin-bottom: 7px;
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  color: var(--color-a);
}
.mini-card__text {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: var(--color-a);
  max-height: 100px;
  overflow: hidden;
  text-overflow: ellipsis;
}

.big-card {
  position: absolute;
  width: 100%;
  top: 50%;
  left: 0;
  right: 0;
  z-index: 20;
  transform: translateX(110%) translateY(-50%);
  opacity: 0;
  transition: all 0.6s;
}
.big-card.show {
  opacity: 1;
  transform: translateX(0) translateY(-50%);
}
.big-card__hide {
  position: absolute;
  top: 50px;
  right: 50px;
  transform: translate(100%, -100%);
  z-index: 5;
}
.big-card__hide svg {
  fill: var(--color-c);
  width: clamp(16px, 1.389vw, 20px);
  height: clamp(16px, 1.389vw, 20px);
}
.big-card__article {
  padding: clamp(20px, 3.472vw, 50px) 50px;
  display: flex;
  flex-direction: column;
  grid-gap: clamp(25px, 2.083vw, 30px);
}
.big-card__article p {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 400;
  font-size: clamp(12px, 1.111vw, 16px);
  line-height: 130%;
  color: var(--color-a);
}
.big-card__top {
  display: flex;
  align-items: center;
  grid-gap: 19px 25px;
}
.big-card__title {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 700;
  font-size: clamp(18px, 1.667vw, 24px);
  line-height: 120%;
  text-transform: uppercase;
  color: var(--color-a);
}

header {
  position: absolute;
  z-index: 20;
  top: clamp(25px, 3.472vw, 50px);
  left: clamp(25px, 5.385vw, 70px);
}

footer {
  position: absolute;
  z-index: 20;
  bottom: clamp(25px, 3.472vw, 50px);
  left: clamp(25px, 5.385vw, 70px);
}
footer .container {
  padding: 0;
}

.index-first {
  position: relative;
  z-index: 0;
}
.index-first__box {
  max-width: min(47.222vw, 680px);
  width: 100%;
  margin-left: auto;
}
.index-first__top {
  margin-bottom: 40px;
}
.index-first__video, .index-first__video img {
  border-radius: 30px;
  display: flex;
}
.index-first__video {
  position: relative;
  z-index: 0;
}
.index-first__video::before {
  z-index: -1;
  left: -6px;
  right: -6px;
  bottom: -6px;
  top: -6px;
  content: "";
  position: absolute;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5));
  opacity: 0.5;
  border-radius: 36px;
}
.index-first__image {
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: -1;
  width: clamp(617px, 60.417vw, 870px);
  height: clamp(564px, 55.208vw, 795px);
}

.index-second {
  position: relative;
  z-index: 0;
}
.index-second__image {
  width: 749px;
  height: clamp(533px, 52.708vw, 759px);
  position: absolute;
  z-index: -1;
  right: 0;
  bottom: 0;
}
.index-second__item {
  position: initial !important;
}
.index-second__inner {
  max-width: min(47.917vw, 690px);
  width: 100%;
  margin-left: auto;
  position: relative;
  overflow: hidden;
}
.index-second__mini {
  transition: opacity 0.4s;
}
.index-second__mini.active {
  opacity: 0;
}
@media screen and (min-width: 1025px) {
  .index-second__mini {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 20px;
    align-items: stretch;
  }
}
.index-second__big {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}